html {
  scroll-behavior: smooth;
  font-family: 'Poppins', sans-serif
}

::-webkit-scrollbar {
  background:transparent;
  width:0px;
}

iframe {
  width:100%;
  margin:auto;
  min-height:800px;
  border:none;
  background-color:white;
  content:'These are hosted on Heroku, and can take a minute or two to load'
}

body {
  margin:0px;
  text-shadow: 0px 0px 1px rgba(255,255,255,0.01);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Poppins', sans-serif
}

.cv {
margin-bottom:200px;
}

.cv iframe {
  background-color:transparent;
  width:50vw;
  height:70vw;
  position:relative;
  border:none;
  background-color:transparent;
}



.background {
  /* background-image: url('./img/tim-marshall-ci7Zk_fK9K0-unsplash.jpg'); */
  background-color:#681370;
  background-size: cover;
  background-position: 100% 60%;
  background-attachment: fixed;
  min-height:100vh;
  position:absolute;
  width:100vw;
  margin:0px;
  overflow:hidden;
  animation-name: example;
  animation-duration: 105s;
  animation-iteration-count: infinite;
}
  
  @keyframes example {
      0%   {background-color: #CD0062; }
      50%  {background-color: #00A7CD; }
      100%  {background-color: #CD0062; }
  }

.mainpage {
  padding:6vw;
  margin:0px;
  display: flex
}

.footer {
  position:absolute;
  bottom:6vw;
  left:6vw;
}

.mainpage h1 {
  color:white;
  font-size: calc(26px + 6.5vh);
  font-style: normal;
  font-weight: 100;
  line-height:1;
  margin-top:0px;
  margin-bottom:10px;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
}

.mainpage h2 {
  color:white;
  display:inline-block;
  font-size: calc(14px + 1vh);
  font-style: normal;
  font-weight: 600;
  line-height:1;
  letter-spacing:2px;
  position:relative;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
}

.mainpage h3 {
  color:white;
  font-size: calc(8px + 1vh);
  font-style: normal;
  font-weight: 400;
  display:inline-block;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
  margin-bottom:100px;
}

p {
  color:white;
  margin:0px;
  font-size:.9rem;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
}

.footer p,.footer a {
  border-bottom: 1px solid transparent;
  color:rgba(255,255,255,0.8);
  font-size:.7rem;
  padding-bottom:2px;
  text-decoration:none;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
}

a {
  transition:.3s;
}

a:hover {
  color:rgba(255,255,255,1);
  border-color:white;
  transition:.3s;
}

.footer h3,.footer h2,.footer p,.footer a {
  line-height:2;
  margin:0px !important;
  text-shadow: 0px 0px 1px rgba(255,255,255,.1);
}

.choiceParent {
  display:flex;
  flex-wrap:wrap;
  justify-content:space-evenly;
  position:absolute;
  bottom:50%;
  width:100%;
  margin:-6vw;
}

.button {
  color:white;
  cursor: pointer;
  display:inline-block;
  flex:1;
  font-size:12px;
  font-weight:700 !important;
  text-transform:uppercase;
  letter-spacing:2px;
  height:15px;
  line-height:1.5;
  padding:15px 30px;
  text-align:center;
  text-shadow: 0px 0px 1px rgba(0,0,0,0.1);
  width:150px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.menuButton {
  position:absolute;
  right:6vw;
  top:6vw;
  user-select:none;
}

.buttonDivider {
  margin:-2px;
}

.portfolioNav{
  position:absolute;
  right:6vw;
}

.white,.menuButton {
  background-color:transparent;
  border:2px solid white;
}

.white:hover,.menubutton:hover {
  background-color:white;
  color: #232b3a;
}

.languages {
  display:flex;
  flex-wrap:wrap;
  min-height:150vh;
}

.languageschild {
  color:white;
  flex:1;
  font-size:20px;
  display:inline-block;
  margin:20px;
  min-width:400px;
  margin-bottom:50px;
}

.languageschild img {
  float:left;
  margin-right:20px;
  width:40px;
}

.languageschild div {
  color:white;
  flex:1;
  text-transform:initial;
  font-size:16px;
  font-weight:400;
  line-height:2;
  margin:40px 0px;
  text-shadow: 0px 0px 20px rgba(0,0,0,.5), 0px 0px 1px rgba(49, 37, 37, 0.001);
}

.spread-parent {
  display:flex;
  flex-wrap:wrap;
}

.spread {
  min-height:800px;
  min-width:600px;
  margin:100px auto;
  position:relative;
  flex:1;
}

.spread img {
  max-height:400px;
  max-width:400px;
  transition:.3s;
  position:absolute;
  box-shadow:0px 0px 20px rgba(0,0,0,0.2);
}

.spread :nth-child(2){
  transform:rotate(2deg);
  left:5vw;
  top:80px;
}

.spread :nth-child(3){
  transform:rotate(-15deg) scale(1.1);
  left:20vw;
  top:250px;
}

.spread :nth-child(4){
  transform:rotate(20deg);
  left:10vw;
  top:450px;
}

