.spread img:hover ~ .spread img{
  border:solid 4px blue;
}

.web-development-parent {
  display:flex;
  flex-wrap:wrap;
  justify-content:space-between;
}

.web-development {
  flex:1;
  width:100%;
  min-width:400px;
  min-height:400px;
  margin:100px 0px;
}


@media only screen and (max-width:600px) {
  .mainpage h1 {
    font-size:calc(26px + 2.5vh)
  }

  .mainpage h2 {
    font-size: calc(8px + 1.5vh)
  }

  .mainpage h3 {
    font-size: calc(6px + 1vh);
  }

  .button {
    font-size: 10px;
    padding: 10px 20px;
  }
}


